 position: relative;\n    }\n    .captcha-img {\n        background-color: var(--captcha-size-img-background);\n        border-radius: 50%;\n        height: var(--captcha-size-img);\n        position: relative;\n        width: var(--captcha-size-img);\n        canvas {\n            height: var(--captcha-size-img);\n            width: var(--captcha-size-img);\n        }\n        img {\n            width: 60px;\n            height: 60px;\n            display: block;\n        }\n        .captcha-loader {\n            align-items: center;\n            display: none;\n            justify-content: center;\n            height: 100%;\n            width: 100%;\n            color: var(--captcha-theme);\n            & > svg {\n                fill: currentColor;\n            }\n        }\n        &.captcha-loading {\n            canvas {\n                display: none;\n            }\n            .captcha-loader {\n                display: flex;\n            }\n        }\n    }\n    .captcha-mask {\n        background-color: var(--captcha-mask-bar-color);\n        bottom: 0;\n        left: 0;\n        position: fixed;\n        right: 0;\n        top: 0;\n    }\n    &.on .captcha-mask {\n        animation: vanishIn .3s;\n    }\n    &.off .captcha-mask {\n        animation: vanishOut .3s;\n        background-color: rgba(0, 0, 0, 0);\n    }\n    .captcha-modal {\n        backface-visibility: hidden;\n        background-color: var(--captcha-modal-background);\n        border-radius: 10px;\n        color: var(--captcha-modal-color);\n        display: inline-block;\n        margin-left: auto;\n        margin-right: auto;\n        max-width: 100%;\n        perspective: 2000px;\n        position: relative;\n        vertical-align: middle;\n        z-index: 1;\n        &-close, &-close svg {\n            height: 15px;\n            width: 15px;\n            color: var(--captcha-modal-title-color);\n            fill: currentColor;\n        }\n        &-close {\n            background-color: transparent;\n            border: none;\n            cursor: pointer;\n            padding: 0;\n            position: absolute;\n            right: 14px;\n            top: 10px;\n            z-index: 1;\n        }\n        &-close-disabled {\n            cursor: not-allowed;\n            opacity: .3;\n        }\n    }\n    &.on .captcha-modal {\n        animation: modalIn .3s;\n    }\n    &.off .captcha-modal {\n        animation: modalOut .3s;\n        opacity: 0;\n    }\n    .captcha-reset {\n        min-width: 76px;\n        text-align: right;\n        button {\n            align-items: center;\n            background-color: transparent;\n            border: none;\n            cursor: pointer;\n            display: inline-flex;\n            padding: 0;\n        }\n        svg {\n            display: inline-block;\n            margin-left: 3px;\n            &.loading {\n                animation: rotating 1.1s linear infinite;\n            }\n        }\n        &.disabled button {\n            cursor: not-allowed;\n        }\n    }\n    .captcha-tips {\n        align-items: center;\n        display: flex;\n        min-width: 0;\n        position: relative;\n        svg {\n            display: inline-block;\n            margin-right: 3px;\n        }\n        .captcha-tooltip-shot {\n            display: block;\n            max-width: 100%;\n            overflow: hidden;\n            text-overflow: ellipsis;\n            white-space: nowrap;\n        }\n    }\n    .captcha-title {\n        color: var(--captcha-modal-title-color);\n        font-size: 16px;\n        font-weight: bold;\n        line-height: 16px;\n        padding-bottom: 10px;\n    }\n    .captcha-tooltips {\n        align-items: center;\n        display: flex;\n        font-size: 14px;\n        justify-content: space-between;\n        margin: 15px auto 0;\n        width: var(--captcha-size-control);\n        svg {\n            width: 16px;\n            height: 16px;\n            fill: currentColor;\n        }\n    }\n    .captcha-wrap {\n        align-items: center;\n        display: flex;\n        justify-content: center;\n    }\n    .tips-success {\n        color: #082;\n    }\n    .tips-fail {\n        color: #f00;\n    }\n}\n/* state */\n.rotate-captcha {\n    .captcha-status-show {\n        canvas {\n            opacity: var(--captcha-canvas-opacity);\n        }\n        .captcha-img {\n            background-color: rgba(0, 0, 0, 1);\n        }\n    }\n}\n.captcha-image {\n    .captcha-state {\n        animation: fadeIn 0.3s forwards;\n        border-radius: 50%;\n        display: none;\n        height: 100%;\n        left: 0;\n        position: absolute;\n        transform: translate(0, 0);\n        top: 0;\n        width: 100%;\n        .captcha-state-icon {\n            height: 100%;\n            width: 100%;\n            & > svg {\n                height: 100%;\n                width: 100%;\n            }\n        }\n        .captcha-state-icon {\n            display: none;\n        }\n    }\n}\n.captcha-status-show {\n    .captcha-image {\n        .captcha-state, .captcha-state-icon {\n            display: block;\n        }\n    }\n}\n.captcha-status-4 .captcha-image {\n    .captcha-state {\n        animation: none;\n        transform: none;\n        .captcha-state-icon {\n            display: flex;\n            align-items: center;\n            justify-content: center;\n            & > svg {\n                height: auto;\n                width: auto;\n            }\n        }\n    }\n}\n.loading_svg__captcha-spin {\n    stop-color: var(--captcha-theme);\n}\n.captcha-status-4 {\n    .loading_svg__captcha-spin {\n        stop-color: rgba(255, 255, 255, 1);\n    }\n}\n/* success timer progress - @deprecated */\n.captcha {\n    .captcha-timer-progress-bar {\n        background-color: var(--captcha-progress-bar-color);\n        height: 4px;\n        width: 100%;\n        display: flex;\n        position: absolute;\n        bottom: 0;\n        left: 0;\n        cursor: pointer;\n        &:hover {\n            transition: width 1.1s linear;\n            width: 0%;\n        }\n    }\n    .captcha-timer-progress-bar-wrap {\n        border-radius: 10px;\n        bottom: 0;\n        display: none;\n        height: 100%;\n        left: 0;\n        overflow: hidden;\n        position: absolute;\n        right: 0;\n        width: 100%;\n    }\n}\n/* animation */\n@keyframes modalIn {\n    0% {\n        -ms-transform: scale(1.185);\n        transform: scale(1.185);\n        opacity: 0;\n    }\n    100% {\n        -ms-transform: scale(1);\n        transform: scale(1);\n        opacity: 1;\n    }\n}\n@keyframes modalOut {\n    0% {\n        -ms-transform: scale(1);\n        transform: scale(1);\n        opacity: 1;\n    }\n    100% {\n        -ms-transform: scale(1.185);\n        transform: scale(1.185);\n        opacity: 0;\n    }\n}\n@keyframes tiping {\n    0% {\n        bottom: 100%;\n        opacity: 0;\n    }\n    100% {\n        bottom: 150%;\n        opacity: 1;\n    }\n}\n@keyframes reback {\n    0% {\n        transform: var(--captcha-control-x);\n    }\n    100% {\n        transform: translateX(0);\n    }\n}\n@keyframes rotating {\n    0% {\n        transform: rotate(0);\n    }\n    100% {\n        transform: rotate(360deg);\n    }\n}\n@keyframes vanishIn {\n    0% {\n        opacity: 0;\n    }\n    100% {\n        opacity: 1;\n    }\n}\n@keyframes vanishOut {\n    0% {\n        opacity: 1;\n    }\n    100% {\n        opacity: 0;\n    }\n}"],"sourceRoot":""}]);
